.submit {
  background-color: green;
  cursor: pointer;
  /*color: white;*/
  border-radius: 3px;
  /*border: 2px solid #0099CC;*/
}

.disableSubmit {
  background-color: red;
  /*color: white;*/
  border-radius: 3px;
}
input {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #9e9e9e;
  border-radius: 0;
  outline: none;
  height: 3rem;
  width: 100%;
  font-size: 1rem;
  margin: 0 0 20px 0;
  padding: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  color: #000000;
}
textarea {
  width: 100%;
}
button {
  height: 40px;
  width: 100px;
  border-radius: 5px;
  outline: none;
  border: none;
}

